<template lang="pug">
  div.mx-3
    v-text-field(label="ID or URL",
            placeholder="media or https://wiki.kirafan.moe/#/standpic/media"
            v-model="input_URL"
            @keydown.enter="go"
            )
    v-btn(@click="go")
      span GO

</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      show: false,
      input_URL:undefined,

    };
  },
  methods:{
    go(){
      const id = this.input_URL.replace(/^[^#]*#\/standpic\//,"")
      this.$router.push('/standpic/' + id);
      // console.log(this.input_URL);
    }
  }
};
</script>